'use client';

import { useState } from 'react';
import { mockVehicles, mockContacts } from '@/data/mockData';
import { MaintenanceAlert, Contact } from '@/types';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog';
import {
  ArrowLeft,
  AlertTriangle,
  Clock,
  Mail,
  Phone,
  Send,
  CheckCircle,
  Bell,
  Users
} from 'lucide-react';
import Link from 'next/link';

export default function NotificationsPage() {
  const [sentNotifications, setSentNotifications] = useState<string[]>([]);
  const [selectedAlert, setSelectedAlert] = useState<MaintenanceAlert | null>(null);
  const [isSendDialogOpen, setIsSendDialogOpen] = useState(false);

  // Generate maintenance alerts from mock data
  const generateAlerts = (): MaintenanceAlert[] => {
    const alerts: MaintenanceAlert[] = [];
    const now = new Date();

    mockVehicles.forEach(vehicle => {
      vehicle.devices.forEach(device => {
        const daysUntilDue = Math.ceil((device.nextMaintenanceDate.getTime() - now.getTime()) / (1000 * 60 * 60 * 24));
        const isOverdue = daysUntilDue < 0;
        const isDueSoon = !isOverdue && daysUntilDue <= 7;

        if (isOverdue || isDueSoon) {
          const alert: MaintenanceAlert = {
            id: `${vehicle.id}-${device.id}-alert`,
            deviceId: device.id,
            vehicleId: vehicle.id,
            type: isOverdue ? 'overdue' : 'due_soon',
            message: `${device.name} on ${vehicle.name} is ${isOverdue ? 'overdue' : 'due soon'} for maintenance`,
            priority: device.criticality === 'critical' ? 'critical' :
                     device.criticality === 'high' ? 'high' :
                     isOverdue ? 'high' : 'medium',
            createdAt: new Date(),
            acknowledged: false
          };
          alerts.push(alert);
        }
      });
    });

    return alerts.sort((a, b) => {
      const priorityOrder = { critical: 0, high: 1, medium: 2, low: 3 };
      return priorityOrder[a.priority] - priorityOrder[b.priority];
    });
  };

  const alerts = generateAlerts();
  const overdueAlerts = alerts.filter(a => a.type === 'overdue');
  const dueSoonAlerts = alerts.filter(a => a.type === 'due_soon');

  const handleSendNotification = (alert: MaintenanceAlert, contacts: Contact[], method: 'email' | 'sms') => {
    // Simulate sending notification
    const notificationId = `${alert.id}-${method}-${Date.now()}`;
    setSentNotifications(prev => [...prev, notificationId]);

    // In a real app, this would make API calls to send emails/SMS
    console.log(`Sending ${method} notification for alert:`, alert);
    console.log('To contacts:', contacts);

    setIsSendDialogOpen(false);
    setSelectedAlert(null);
  };

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'critical': return 'bg-red-100 text-red-800 border-red-200';
      case 'high': return 'bg-orange-100 text-orange-800 border-orange-200';
      case 'medium': return 'bg-yellow-100 text-yellow-800 border-yellow-200';
      default: return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  const getAlertIcon = (type: string) => {
    return type === 'overdue' ?
      <AlertTriangle className="h-5 w-5 text-red-600" /> :
      <Clock className="h-5 w-5 text-orange-600" />;
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <Link
            href="/"
            className="inline-flex items-center gap-2 text-gray-600 hover:text-gray-900 transition-colors mb-4"
          >
            <ArrowLeft className="h-5 w-5" />
            Back to Dashboard
          </Link>
          <h1 className="text-3xl font-bold text-gray-900">Maintenance Notifications</h1>
          <p className="mt-1 text-sm text-gray-500">
            Manage and send maintenance alerts for equipment requiring attention
          </p>
          <div className="mt-4">
            <Badge variant="secondary" className="px-3 py-1">
              <Bell className="h-4 w-4 mr-1" />
              {alerts.length} Active Alerts
            </Badge>
          </div>
        </div>
        {/* Summary Cards */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Overdue Maintenance</CardTitle>
              <AlertTriangle className="h-4 w-4 text-red-600" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-red-600">{overdueAlerts.length}</div>
              <p className="text-xs text-muted-foreground">Requires immediate attention</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Due Soon</CardTitle>
              <Clock className="h-4 w-4 text-orange-600" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-orange-600">{dueSoonAlerts.length}</div>
              <p className="text-xs text-muted-foreground">Within 7 days</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Notifications Sent</CardTitle>
              <Send className="h-4 w-4 text-green-600" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">{sentNotifications.length}</div>
              <p className="text-xs text-muted-foreground">This session</p>
            </CardContent>
          </Card>
        </div>

        {/* Alerts List */}
        <div>
          <h2 className="text-2xl font-bold text-gray-900 mb-6">Maintenance Alerts</h2>

          {alerts.length === 0 ? (
            <Card>
              <CardContent className="p-8 text-center">
                <CheckCircle className="h-12 w-12 text-green-600 mx-auto mb-4" />
                <h3 className="text-lg font-medium text-gray-900 mb-2">All Clear!</h3>
                <p className="text-gray-600">No maintenance alerts at this time.</p>
              </CardContent>
            </Card>
          ) : (
            <div className="space-y-4">
              {alerts.map((alert) => {
                const vehicle = mockVehicles.find(v => v.id === alert.vehicleId);
                const device = vehicle?.devices.find(d => d.id === alert.deviceId);
                const contacts = device?.assignedContacts || [];

                return (
                  <Card key={alert.id} className="hover:shadow-md transition-shadow">
                    <CardContent className="p-6">
                      <div className="flex items-start justify-between">
                        <div className="flex items-start gap-4">
                          <div className="mt-1">
                            {getAlertIcon(alert.type)}
                          </div>
                          <div className="flex-1">
                            <div className="flex items-center gap-3 mb-2">
                              <h3 className="font-semibold text-gray-900">
                                {device?.name} - {vehicle?.name}
                              </h3>
                              <Badge className={`${getPriorityColor(alert.priority)} border`}>
                                {alert.priority}
                              </Badge>
                              <Badge variant={alert.type === 'overdue' ? 'destructive' : 'secondary'}>
                                {alert.type.replace('_', ' ')}
                              </Badge>
                            </div>

                            <p className="text-gray-600 mb-3">{alert.message}</p>

                            <div className="flex items-center gap-6 text-sm text-gray-500">
                              <div className="flex items-center gap-1">
                                <Users className="h-4 w-4" />
                                <span>{contacts.length} assigned contacts</span>
                              </div>
                              <div>
                                Created: {alert.createdAt.toLocaleDateString()}
                              </div>
                            </div>
                          </div>
                        </div>

                        <div className="flex gap-2">
                          <Dialog open={isSendDialogOpen && selectedAlert?.id === alert.id} onOpenChange={(open) => {
                            setIsSendDialogOpen(open);
                            if (open) setSelectedAlert(alert);
                          }}>
                            <DialogTrigger>
                              <Button size="sm" variant="outline">
                                <Send className="h-4 w-4 mr-2" />
                                Send Reminder
                              </Button>
                            </DialogTrigger>
                            <DialogContent>
                              <DialogHeader>
                                <DialogTitle>Send Maintenance Reminder</DialogTitle>
                                <DialogDescription>
                                  Send notification for: {device?.name} on {vehicle?.name}
                                </DialogDescription>
                              </DialogHeader>
                              <div className="space-y-4">
                                <div>
                                  <h4 className="font-medium mb-2">Assigned Contacts:</h4>
                                  <div className="space-y-2">
                                    {contacts.map((contact) => (
                                      <div key={contact.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-md">
                                        <div>
                                          <p className="font-medium">{contact.name}</p>
                                          <p className="text-sm text-gray-600">{contact.role}</p>
                                        </div>
                                        <div className="flex gap-2">
                                          {contact.email && (
                                            <Button
                                              size="sm"
                                              variant="outline"
                                              onClick={() => handleSendNotification(alert, [contact], 'email')}
                                              disabled={sentNotifications.includes(`${alert.id}-email-${contact.id}`)}
                                            >
                                              <Mail className="h-4 w-4 mr-1" />
                                              {sentNotifications.includes(`${alert.id}-email-${contact.id}`) ? 'Sent' : 'Email'}
                                            </Button>
                                          )}
                                          {contact.phone && (
                                            <Button
                                              size="sm"
                                              variant="outline"
                                              onClick={() => handleSendNotification(alert, [contact], 'sms')}
                                              disabled={sentNotifications.includes(`${alert.id}-sms-${contact.id}`)}
                                            >
                                              <Phone className="h-4 w-4 mr-1" />
                                              {sentNotifications.includes(`${alert.id}-sms-${contact.id}`) ? 'Sent' : 'SMS'}
                                            </Button>
                                          )}
                                        </div>
                                      </div>
                                    ))}
                                  </div>
                                </div>
                              </div>
                              <DialogFooter>
                                <Button variant="outline" onClick={() => setIsSendDialogOpen(false)}>
                                  Close
                                </Button>
                              </DialogFooter>
                            </DialogContent>
                          </Dialog>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                );
              })}
            </div>
          )}
        </div>
      </main>
    </div>
  );
}